.icon-left {
  position: absolute;
  top: 10px;
  left: 10px;
}
.icon-right {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.season-display.summer i {
  color:yellow;
}
.season-display.winter i {
  color:blue;
}

.season-display {
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}

.summer {
  background-color: orange;
}

.winter {
  background-color: aliceblue;
}
